@import "../base/_variables.scss";

.rating {
	display: flex;
	gap: 5px;
	margin-top: 20px;

	@media only screen and (max-width: $bp-tablet) {
		margin-top: 0;
	}

	img {
		width: 25px;
		height: 25px;

		@media only screen and (max-width: $bp-tablet) {
			width: 17px;
			height: 17px;
		}
	}
}
